import { ImageResponse } from '@vercel/og';
import { NextRequest } from 'next/server';
import React from 'react';

export const config = {
  runtime: 'edge',
};

async function getImageDimensions(url: string): Promise<{ width: number; height: number }> {
  const response = await fetch(`/api/dimensions?url=${encodeURIComponent(url)}`);
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(errorData.error || 'Failed to get image dimensions');
  }
  return response.json();
}

export default async function handler(req: NextRequest) {
  try {
    const { searchParams } = new URL(req.url);
    const text = searchParams.get('text') || 'Default Text';
    const imageUrl = searchParams.get('imageUrl');

    if (!imageUrl) {
      throw new Error('No image URL provided');
    }

    const fontSize = parseInt(searchParams.get('fontSize') || '40');
    const fontColor = searchParams.get('fontColor') || 'white';
    const x = parseInt(searchParams.get('x') || '10');
    const y = parseInt(searchParams.get('y') || '50');

    console.log(`Fetching dimensions for image: ${imageUrl}`);
    let { width, height } = await getImageDimensions(imageUrl);
    console.log(`Image dimensions: ${width}x${height}`);

    const element = React.createElement(
      'div',
      {
        style: {
          fontSize,
          color: fontColor,
          background: `url(${imageUrl})`,
          backgroundSize: '100% 100%',
          width: '100%',
          height: '100%',
          display: 'flex',
          alignItems: 'flex-start',
          justifyContent: 'flex-start',
          padding: `${y}px 0 0 ${x}px`,
        },
      },
      text
    );

    return new ImageResponse(element, {
      width,
      height,
    });
  } catch (error: unknown) {
    const errorMessage = error instanceof Error ? error.message : 'An unknown error occurred';
    console.error(`Error in overlay API: ${errorMessage}`);
    return new Response(`Failed to generate the image: ${errorMessage}`, {
      status: 500,
    });
  }
}